export default {
	data() {
		return {
			farmProduceList:{} ,
			treeDetails:[],
		}
	},
	onLoad() {
		this.isLogin();
		this.farmProduceInfo();
	},
	onShow() {
		this.getTreeDetails()
	},
	methods: {
		getTreeDetails() {
			let that = this;
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'user_tree_details'
				}),
				function(res) {
					that.treeDetails=res.data;
					console.log(res);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToMap() {
			let that=this;
			uni.navigateTo({
				url:"/pages/map/map?mapInfo="+JSON.stringify(that.farmProduceList.merInfo[0].list[0])
			})
		},
		farmProduceInfo() {
			let that = this;
			that.goodsList = [];
			that.baseGet(
				that.U({
					c: 'store_api',
					a: 'get_merchant_index'
				}),
				function(res) {
					that.farmProduceList = res.data;
					console.log(res);
				},
				function(res) {
					console.log(res);
				},
				true
			);
		},
		navToMoreTreeList(){
			uni.navigateTo({
				url:'/pages/tabber/home/more_tree_list/more_tree_list'
			})
		},
	}
}
